import * as constants from './constants';

const defaultState = {
  // 公号接管
  gonghaoTagList: [],
  allGonghaoList: [],
  originalGonghaoList: [],
  gonghaoList: [],
  currentPage: 1,
  searchName: "",
  isSelectTag: false,
  selectedTag: "",
  isCreate: true,
  gonghaoModal: false,
  gonghaoInfo: {
    group: "",
    id: "",
    attribute: 0
  },
  // 公号配置
  configList: [],
  configForm: {type: 0, sex: 3, attribute: 1, text: "", replyType: 0, msgId: null, key: "", urlList: []},

};

export default (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state)), _start, _end;
  switch (action.type) {
    case constants.GET_GONGHAO_TAG_LIST:
      newState.gonghaoTagList = action.gonghaoTagList;
      return newState;
    case constants.GET_ALL_GONGHAO_LIST:
      newState.allGonghaoList = newState.originalGonghaoList = action.allGonghaoList;
      newState.gonghaoList = action.gonghaoList;
      return newState;
    case constants.CHANGE_PAGE:
      _start = (action.page - 1) * 10;
      _end = action.page * 10;
      newState.gonghaoList = newState.allGonghaoList.slice(_start, _end);
      newState.currentPage = action.page;
      return newState;
    case constants.CHANGE_SEARCH_INPUT:
      newState.searchName = action.searchName;
      return newState;
    case constants.SEARCH_BY_NAME:
      newState.allGonghaoList = action.allGonghaoList;
      newState.gonghaoList = action.gonghaoList;
      newState.searchName = "";
      return newState;
    case constants.OPEN_GONGHAO_MODAL:
      newState.gonghaoModal = true;
      newState.gonghaoInfo = {id: action.id, group: action.group, attribute: action.attribute};
      return newState;
    case constants.CLOSE_GONGHAO_MODAL:
      newState.gonghaoModal = false;
      newState.gonghaoInfo = {attribute: 0};
      return newState;
    case constants.CHANGE_GROUP:
      newState.gonghaoInfo.group = action.name;
      return newState;
    case  constants.CHANGE_ATTRIBUTE:
      newState.gonghaoInfo.attribute = action.attribute;
      return newState;
    case constants.DELETE_GONGHAO:
      newState.gonghaoList.splice(action.index, 1);
      return newState;
    case constants.SESECT_TAG:
      newState.isSelectTag = true;
      newState.selectedTag = action.selectedTag;
      return newState;
    case constants.REMOVE_TAG:
      newState.isSelectTag = false;
      newState.selectedTag = "";
      return newState;
    case constants.TAG_CONFIRM:
      newState.isSelectTag = true;
      newState.selectedTag = action.group;
      action.name && newState.gonghaoTagList.push(action.name);
      return newState;
    case constants.DELETE_TAG_ITEM:
      newState.isSelectTag = false;
      newState.selectedTag = "";
      newState.gonghaoTagList.splice(action.index, 1);
      return newState;
    case constants.CHECK_PAGE_ATTR:
      newState.isCreate = false;
      return newState;
    // 公号配置
    case constants.GET_CONFIG_LIST:
      newState.configList = action.configList;
      return newState;
    case constants.DELETE_CONFIG:
      newState.configList.splice(action.index, 1);
      return newState;
    case constants.CHANGE_CONFIG_FORM:
      newState.configForm = {...action.formInfo};
      return newState;

    default:
      return state;
  }
};