import React, { Component } from 'react';
import { Checkbox, Row, Col, Modal, Button } from 'antd';
import axios from 'axios';

class MultiSelectGonghao extends Component {

  constructor(props) {
    super(props);
    this.state = {
      codes: props.codes || []
    };
  }

  onChange = codes => {
    this.setState({codes})
  };

  confirmSelect = () => {
    const { codes } =this.state;
    this.props.confirmSelect(codes);
  };

  render() {
    const {selectGonghaoModal, allCodes} = this.props;
    const { codes } = this.state;
    return (
      <Modal width={1000} visible={selectGonghaoModal} maskClosable={false} closable={false} mask={false}  footer={<Button type="primary" disabled={codes.length === 0} onClick={this.confirmSelect}>确定</Button>}>
        <Checkbox.Group value={codes} style={{ width: '100%' }} onChange={this.onChange}>
          <Row>
            {allCodes.map(item =>  <Col span={4} key={item._id}>
              <Checkbox value={item.code}>{item.nick_name}</Checkbox>
            </Col>)}
          </Row>
        </Checkbox.Group>
      </Modal>
    )
  }
}

export default MultiSelectGonghao;