import * as constants from './constants';

const defaultState = {
  materialList: [],
  content: {
    picurl: "",
    title: "",
    url: "",
    description: ""
  },
  materialInfo: {
    type: 0,
    contents: [],
    description: ""
  }
};

export default (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state));
  switch (action.type) {
    case constants.GET_MATERIAL_LIST:
      newState.materialList = action.materialList;
      return newState;
    case constants.DELETE_MATERIAL:
      newState.materialList.splice(action.index, 1);
      return newState;
    case constants.HANDLE_CONTENT_CHANGE:
      newState.content[action.name] = action.value;
      return newState;
    case constants.HANDLE_MATERIAL_CHANGE:
      if(action.name === "type") {
        newState.content = defaultState.content;
        newState.materialInfo.contents = [];
      }
      newState.materialInfo[action.name] = action.value;
      return newState;
    case constants.INIT_MATERIAL_INFO:
      newState.materialInfo = {...action.record};
      newState.materialInfo.id = newState.materialInfo._id;
      newState.content = {...newState.materialInfo.contents[0]};
      return newState;
    case constants.COPY:
      newState.materialList.unshift({...action.data});
      return newState;
    case constants.RESET_STATE:
      newState = defaultState;
      return newState;
    default:
      return state;
  }
};