import axios from 'axios';
import * as constants from './constants';
import { Modal, message } from 'antd';


export const getMenuList = () => {
  return dispatch => {
    axios.get('/menu')
      .then(res => {
        if (res.data.data.length > 0) {
          let menuList = res.data.data.reverse();
          const action = {
            type: constants.GET_MENU_LIST,
            menuList
          };
          dispatch(action);
        }
      })
  }
};

export const deleteMenu = (id, index) => {
  return dispatch => {
    Modal.confirm({
      title: "提示",
      content: "确定删除吗？",
      onOk: () => {
        axios.get("/menu/del", {params: {id: id}})
          .then(res => {
            if(res.data.success) {
              const action = {
                type: constants.DELETE_MENU,
                index
              };
              dispatch(action);
              message.success(res.data.success)
            }
          });
      }
    })
  }
};

