export const submitMenu = props => {
  return dispatch => {
    Modal.confirm({
      title: "提示",
      content: "确定提交吗？",
      onOk: () => {
        axios.post(props.history.location.query ? "/menu/update" : "/menu/create", {...props.menuInfo})
          .then(res => {
            const action = {
              type: constants.SUBMIT_MENU
            };
            dispatch(action);
            props.history.goBack();
            resetState();
          })
      }
    });
  }
};

