import React, { Component } from 'react';
import { Table, Select } from "antd";
import { connect } from "react-redux";
import { actionCreators } from './store';
import { Link } from "react-router-dom";
import Button from '../../../common/components/Button';

const { Option } = Select;

class Kefu extends Component {

  state = {
    messageType: "请选择消息类型"
  };

  componentWillMount() {
    this.props.getKefuMessageList()
  }

  render() {
    const { kefuMessageList, copyMessage, deleteMessage, sendKefuMessage, messageType, handleChangeType }  = this.props;
    const kefuMessageHeader = [
      {
        key: "type",
        title: "类型",
        align: "center",
        width: 50,
        render: (text, record) => (record.type === 1 ? "文本" : record.type === 0 ? "图文" : "图片") + (record.contents.length > 1 ? "(多)" : "")
      },
      {
        key: "task",
        title: "是否延时",
        align: "center",
        width: 50,
        render: (text, record) => record.task ? "是" : "否"
      },
      {
        key: "delay",
        title: "延时时间(分钟)",
        align: "center",
        width: 80,
        render: (text, record) => record.task ? record.delay : "——"
      },
      {
        key: "is_timing",
        title: "是否定时",
        align: "center",
        width: 50,
        render: (text, record) => record.is_timing ? "是" : "否"
      },
      {
        key: "timing_time",
        title: "定时时间(分钟)",
        align: "center",
        width: 100,
        sorter: (a, b) => (new Date(a.timing_time)).getTime() - (new Date(b.timing_time)).getTime(),
        render: (text, record) => record.is_timing ? record.time : "——"
      },
      {
        key: "sex",
        title: "性别",
        align: "center",
        dataIndex: "sex",
        width: 50,
        render: text => {
          return text === "0" ? "未知" : text === "1"  ? "男" : text === "2" ? "女" : "全部";
        }
      },
      {
        key: "action_type",
        title: "互动行为",
        align: "center",
        dataIndex: "action_type",
        width: 100,
        render: text => {
          return text === 0 ? "全部" : text === 3  ? "用户回复" : text === 2 ? "点击菜单" : "用户关注";
        }
      },
      {
        key: "remarks",
        title: "备注",
        dataIndex: "remarks",
        align: "center",
        render: text => !text ? "请点击修改，添加备注信息" : text
      },
      {
        title: "Action",
        align: "center",
        width: 280,
        render: (text, record, index) => (
          <div>
            <Button background="success" onClick={() => copyMessage(record)} buttonContent="复制" />
            <Button buttonContent={<Link to={{pathname: "./update", query: {...record}}}>修改</Link>} />
            <Button disabled={record.is_timing || record.delay} background={(record.is_timing || record.delay) ? "info" : "warning"} onClick={() => sendKefuMessage(record._id, record.tagId)} buttonContent="发送消息" />
            <Button background="danger" onClick={() => deleteMessage(record._id, index)} buttonContent="删除" />
          </div>
        )
      }
    ];
    return (
    	<div>
        <div className="container">
          <Button size="default" mb={20} buttonContent={<Link to="./create">新建客服消息</Link>} />
          <Select value={messageType} onChange={handleChangeType}>
            <Option value={3}>全部</Option>
            <Option value={0}>手动</Option>
            <Option value={1}>延时</Option>
            <Option value={2}>定时</Option>
          </Select>
          <Table bordered size="middle" rowKey={record => record._id} columns={kefuMessageHeader} dataSource={kefuMessageList} />
        </div>
      </div>
    )
  }
}

const mapState = state => ({
  kefuMessageList: state.message.kefu.kefuMessageList,
  messageType: state.message.kefu.messageType
});

const mapDispatch = dispatch => ({
  getKefuMessageList() {
    dispatch(actionCreators.getKefuMessageList())
  },
  copyMessage(record) {
    dispatch(actionCreators.createKefuMessage(record))
  },
  deleteMessage(id, index) {
    dispatch(actionCreators.deleteMessage(id, index))
  },
  sendKefuMessage(id, tagId) {
    dispatch(actionCreators.sendKefuMessage(id, tagId))
  },
  handleChangeType(value) {
    dispatch(actionCreators.handleChangeType(value))
  }
});

export default connect(mapState, mapDispatch)(Kefu);