import React, {Component} from 'react';
import {Input, Card, List, Modal, Form, Select, Radio} from "antd";
import {connect} from "react-redux";
import {Link} from 'react-router-dom';
import {actionCreators} from './store';
import './index.scss';
import Button from "../../../common/components/Button";

const {Search} = Input;
const {Option} = Select;

class Tuoguan extends Component {

  componentDidMount() {
    this.props.getGroupList();
    this.props.getAllGonghaoList();
  }

  render() {
    const {gonghaoTagList, gonghaoList, searchByName, searchName, changeSearchInput, jieguan, addNewGonghao, openGonghaoModal, gonghaoModal, closeGonghaoModal, gonghaoInfo, updateGonghaoInfo, changeGroup, changeAttribute} = this.props;
    return (
      <div>
        <div className='container'>
          <Search
            placeholder='请输入公号名称'
            className='search-btn'
            value={searchName}
            onChange={changeSearchInput}
            onSearch={searchName => searchByName(searchName)}
          />
          <div className="fr btn-wrapper">
            <Button background="success" size="default"
                    buttonContent={<Link to='/admin/gonghao/tuoguan/group'>分组管理</Link>}/>
            <Button size="default" buttonContent="添加新公号" onClick={addNewGonghao}/>
          </div>
          <div className='table-wrapper'>
            <List
              grid={{gutter: 16, column: 4}}
              dataSource={gonghaoList}
              pagination={{pageSize: 12}}
              renderItem={item => (
                <List.Item>
                  <Card className="card-item">
                    <p>{item.nick_name}</p>
                    <ul>
                      <li onClick={() => openGonghaoModal(item._id, item.group, item.attribute)}>修改</li>
                      <li><Link to={{pathname: "config/index", state:{code: item.code}}}>配置</Link></li>
                      <li className={item.status === -2 ? "" : "disabled-click"}
                          onClick={() => item.status === -2 ? jieguan(item.code) : console.log("已接管或接管中")}>{item.status === -2 ? "接管" : (item.status === -1 ? "接管中" : "已接管")}</li>
                      {/*<li>解绑</li>*/}
                    </ul>
                  </Card>
                </List.Item>
              )}
            />
          </div>
        </div>
        <Modal visible={gonghaoModal} title="修改公号信息" onCancel={closeGonghaoModal} onOk={() => updateGonghaoInfo(gonghaoInfo)}>
          <Form labelCol={{span: 6}} wrapperCol={{span: 14}}>
            <Form.Item label="分组">
              <Select value={gonghaoInfo.group || "未分组"} onChange={changeGroup}>
                <Option value="未分组">未分组</Option>
                {gonghaoTagList.map(item => <Option key={item._id} value={item.name}>{item.name}</Option>)}
              </Select>
            </Form.Item>
            <Form.Item label="公号属性">
              <Radio.Group value={gonghaoInfo.attribute} onChange={changeAttribute}>
                <Radio value={0}>默认</Radio>
                <Radio value={1}>男</Radio>
                <Radio value={2}>女</Radio>
              </Radio.Group>
            </Form.Item>
          </Form>
        </Modal>
      </div>
    )
  }
}

const mapState = state => ({
  gonghaoTagList: state.gonghao.tuoguan.gonghaoTagList,
  allGonghaoList: state.gonghao.tuoguan.allGonghaoList,
  gonghaoList: state.gonghao.tuoguan.gonghaoList,
  currentPage: state.gonghao.tuoguan.currentPage,
  searchName: state.gonghao.tuoguan.searchName,
  gonghaoModal: state.gonghao.tuoguan.gonghaoModal,
  gonghaoInfo: state.gonghao.tuoguan.gonghaoInfo,
});

const mapDispatch = dispatch => ({
  getGroupList() {
    dispatch(actionCreators.getGroupList())
  },
  getAllGonghaoList() {
    dispatch(actionCreators.getAllGonghaoList())
  },
  changePage(page) {
    dispatch(actionCreators.changePage(page))
  },
  changeSearchInput(e) {
    dispatch(actionCreators.changeSearchInput(e.target.value))
  },
  searchByName(value) {
    dispatch(actionCreators.searchByName(value));
  },
  deleteGonghao(id, index) {
    dispatch(actionCreators.deleteGonghao(id, index));
  },
  jieguan(code) {
    dispatch(actionCreators.jieguan(code));
  },
  addNewGonghao() {
    dispatch(actionCreators.addNewGonghao());
  },
  openGonghaoModal(id, group, attribute) {
    dispatch(actionCreators.openGonghaoModal(id, group, attribute));
  },
  closeGonghaoModal() {
    dispatch(actionCreators.closeGonghaoModal());
  },
  updateGonghaoInfo(gonghaoInfo) {
    dispatch(actionCreators.updateGonghaoInfo(gonghaoInfo));
  },
  changeGroup(name) {
    dispatch(actionCreators.changeGroup(name));
  },
  changeAttribute(e) {
    let attribute = e.target.value;
    dispatch(actionCreators.changeAttribute(attribute));
  }
});

export default connect(mapState, mapDispatch)(Tuoguan);