import * as constants from './constants';

const defaultState = {
  menuList: [],
  menuIndex: -1,
  secMenuIndex: -1,
  isSubmit: false,
  allCodes: [],
  targetKeys: [],
  sub_button: {
    name: "子菜单名称",
    type: "view",
    key: "",
    url: ""
  },
  menuForm: {
    name: "菜单名称",
    type: "view",
    key: "",
    url: "",
    sub_button: []
  },
  menuInfo: {
    title: "",
    individual: false,
    sex: "0",
    values: [],
    codes: []
  }
};

export default (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state));
  switch (action.type) {
    case constants.GET_MENU_LIST:
      newState.menuList = action.menuList;
      return newState;
    case constants.DELETE_MENU:
      newState.menuList.splice(action.index, 1);
      return newState;
    case constants.HANDLE_INFO_CHANGE:
      newState.menuInfo[action.name] = action.value;
      return newState;
    case constants.HANDLE_INDIVIDUAL_CHANGE:
      newState.menuInfo.individual = action.value;
      Object.assign(newState.menuForm, {
        sex: 0
      });
      return newState;
    case constants.HANDLE_INPUT_CHANGE:
      newState.menuForm[action.name] = action.value;
      action.name === "type" && Object.assign(newState.menuForm, {
        key: "",
        url: ""
      });
      newState.menuInfo.values.splice(newState.menuIndex, 1, {...newState.menuForm})
      return newState;
    case constants.HANDLE_SUB_INPUT_CHANGE:
      newState.sub_button[action.name] = action.value;
      action.name === "type" && Object.assign(newState.sub_button, {
        key: "",
        url: ""
      });
      newState.menuForm.sub_button.splice(newState.secMenuIndex, 1, {...newState.sub_button});
      newState.menuInfo.values.splice(newState.menuIndex, 1, {...newState.menuForm})
      return newState;
    case constants.ADD_FIRST_MENU:
      newState.menuForm = {
        name: "菜单名称",
        type: "view",
        key: "",
        url: "",
        sub_button: []
      };
      newState.menuInfo.values.push({...newState.menuForm});
      newState.menuIndex = newState.menuInfo.values.length - 1;
      return newState;
    case constants.EDIT_MENU:
      newState.menuIndex = action.index;
      newState.menuForm = {...action.data};
      newState.menuForm.sub_button && (newState.sub_button = {...newState.menuForm.sub_button[0]});
      return newState;
    case constants.ADD_SECOND_MENU:
      newState.sub_button = {
        name: "子菜单名称",
        type: "view",
        key: "",
        url: ""
      };
      newState.menuForm.sub_button.push({...newState.sub_button});
      newState.menuInfo.values.splice(newState.menuIndex, 1, {...newState.menuForm});
      newState.secMenuIndex = newState.menuForm.sub_button.length - 1;
      return newState;
    case constants.EDIT_SEC_MENU:
      newState.secMenuIndex = action.index;
      newState.sub_button = {...action.data};
      return newState;
    case constants.DELETE_Fir_MENU:
      newState.menuInfo.values.splice(newState.menuIndex, 1);
      newState.menuIndex = newState.menuInfo.values.length - 1;
      newState.menuForm = newState.menuInfo.values[0] || {
        name: "菜单名称",
        type: "view",
        key: "",
        url: "",
        sub_button: []
      };
      return newState;
    case constants.DELETE_SUB_MENU:
      newState.menuForm.sub_button.splice(newState.secMenuIndex, 1);
      newState.menuInfo.values[newState.menuIndex].sub_button.splice(newState.secMenuIndex, 1);
      newState.secMenuIndex = newState.menuForm.sub_button.length - 1;
      newState.sub_button = newState.menuForm.sub_button[0] || {
        name: "子菜单名称",
        type: "view",
        key: "",
        url: ""
      };
      return newState;
    case constants.GET_ALL_CODES:
      newState.allCodes = [...action.allCodes];
      return newState;
    case constants.CHANGE_GONGHAO:
      newState.menuInfo.codes = newState.targetKeys = [...action.targetKeys];
      return newState;
    case constants.OPEN_MADAL:
      newState.isSubmit = true;
      return newState;
    case constants.SUBMIT_MENU:
      newState.isSubmit = false;
      return newState;
    case constants.CANCEL_MENU:
      newState.isSubmit = false;
      return newState;
    case constants.INIT_MENU_INFO:
      newState.menuInfo = action.record;
      newState.menuInfo.id = action.record._id;
      newState.targetKeys = [...newState.menuInfo.codes];
      newState.menuForm = {...newState.menuInfo.values[0]};
      if(newState.menuForm.sub_button) {
        newState.sub_button = {...newState.menuForm.sub_button[0]};
      }
      return newState;
    case constants.RESET_STATE:
      newState = defaultState;
      return newState;
    default:
      return state;
  }
};