import React, {Component} from 'react';
import {Icon, Form, Button, Radio, DatePicker, Input, Upload, Transfer, Modal, InputNumber, Avatar, Divider  } from "antd";
import {connect} from "react-redux";
import {actionCreators} from '../store';
import moment from "moment";
import "./index.scss";
import MultiSelectGonghao from '../components/MultiSelectGonghao';

class EditMessage extends Component {
  state = {
    selectGonghaoModal: true
  };



  componentWillMount() {
    this.props.getAllCodes();
    let query = this.props.history.location.query;
    console.log(query)
    query && this.props.closeSelectModal();
    query && this.props.initKefuMessageInfo(query);
  }

  render() {
    const { kefuMessageInfo, content, paramsModal, changeContentValue, changeMessageValue, uploadImg, addMessage, selectMessage, hideParamsModal, submitMessage, allCodes, codes, changeGonghao, selectGonghaoModal, history, openSelectModal } = this.props;
    // const mapMaterials = kefuMessageInfo.contents.map((item, index) => (
    //   <li key={item._id || index} onClick={() => selectMessage(item, index)}>
    //     <div className="material-item">
    //       <div className="material-title fl textEllipsis">{item.title || "标题"}</div>
    //       <div className="material-img fl">
    //         <img src={item.picurl} alt=""/>
    //       </div>
    //     </div>
    //   </li>
    // ));
    // isImgAndText,
      // if (kefuMessageInfo.type === 0) {
    //   isImgAndText = <div className="show-material-wrapper fr">
    //     <ul>
    //       {mapMaterials}
    //       {kefuMessageInfo.contents.length < 8 && <li className="add-material tc" onClick={addMessage}><Icon type="plus"/></li>}
    //     </ul>
    //   </div>
    // }
    let isUploadImg, isTextUploadImg, FormItemWrapper;
    if (content.picurl) {
      isTextUploadImg = <img style={{width: "100px"}} src={content.picurl} alt=""/>
    } else {
      isTextUploadImg = <Button><Icon type="upload"/> 上传图片 </Button>
    }
    if (kefuMessageInfo.img) {
      isUploadImg = <img style={{width: "100px"}} src={kefuMessageInfo.img} alt=""/>
    } else {
      isUploadImg = <Button><Icon type="upload"/> 上传图片 </Button>
    }
    if (kefuMessageInfo.type === 1) {
      FormItemWrapper = <Form.Item label="文本消息" className="text-message">
        <Input.TextArea autosize={{minRows: 6}} value={content.description} placeholder="请输入文本消息" onChange={e => changeContentValue(e, "description")}/>
      </Form.Item>
    } else if (kefuMessageInfo.type === 0) {
      FormItemWrapper = <div>
        <Form.Item label="图片封面">
          <Upload name="imageFile" action="/reply/upload" onChange={(info) => {
            uploadImg(kefuMessageInfo, info)
          }} showUploadList={false}>
            {isTextUploadImg}
          </Upload>
        </Form.Item>
        <Form.Item label="图文标题">
          <Input placeholder="请输入图文标题" value={content.title} onChange={e => changeContentValue(e, "title")}/>
        </Form.Item>
        <Form.Item label="图文描述" className="text-message">
          <Input.TextArea autosize={{minRows: 4}} value={content.description} placeholder="请输入图文描述" onChange={e => changeContentValue(e, "description")}/>
        </Form.Item>
        <Form.Item label="跳转链接">
          <Input placeholder="请输入跳转链接" value={content.url} onChange={e => changeContentValue(e, "url")}/>
        </Form.Item>
      </div>
    } else if (kefuMessageInfo.type === 2) {
      FormItemWrapper =
        <Form.Item label="图片消息">
          <Upload name="imageFile" action="/reply/upload" onChange={(info) => {
            uploadImg(kefuMessageInfo, info)
          }} showUploadList={false}>
            {isUploadImg}
          </Upload>
        </Form.Item>;
    }
    const formItemLayout = {
      labelCol: {
        sm: {span: 4}
      },
      wrapperCol: {
        sm: {span: 16, offset: 0}
      },
    };
    let Channel;
    if(kefuMessageInfo.channel === 1) {
      Channel = <><Form.Item label="延时时间">
        <InputNumber formatter={value => `${value}分钟`}
                              parser={value => value.replace('分钟', '')} value={kefuMessageInfo.delay} min={1} onChange={value => changeMessageValue(value, "delay")} />
      </Form.Item>
        <Form.Item label="互动类型">
          <Radio.Group value={kefuMessageInfo.action_type} onChange={e => changeMessageValue(e.target.value, "action_type")}>
            <Radio value={0}>全部</Radio>
            <Radio value={1}>用户关注</Radio>
            <Radio value={3}>用户回复</Radio>
            <Radio value={2}>点击菜单</Radio>
          </Radio.Group>
        </Form.Item>
        </>
    } else if(kefuMessageInfo.channel === 2) {
      Channel =
        <Form.Item label="定时时间">
          <DatePicker showTime placeholder="选择时间" value={moment(kefuMessageInfo.timing_time)} onChange={(value, dateString) => changeMessageValue(dateString, 'timing_time')} />
        </Form.Item>
    }
    return (
      <div>
        <div className="container edit-message">
          {/*{isImgAndText}*/}
          <div className="kefu-form fl">
            <Form {...formItemLayout}>
              <Form.Item label="选择公号">
                <Avatar onClick={openSelectModal} className="cursor" shape="square" icon="user" />
              </Form.Item>
              <Form.Item label="消息名称">
                <Input value={kefuMessageInfo.remarks} placeholder="请输入消息名称,(仅供备注，不会发送给粉丝)" onChange={e => changeMessageValue(e.target.value, "remarks")} />
              </Form.Item>
              <Divider style={{width: "175%", marginLeft: -50}} />
              <h2 className="block-title">设置客服消息</h2>
              <Form.Item>
                <Radio.Group buttonStyle="solid" value={kefuMessageInfo.type} onChange={e => changeMessageValue(e.target.value, "type")}>
                  <Radio.Button value={0}>图文消息</Radio.Button>
                  <Radio.Button value={1}>文本消息</Radio.Button>
                  <Radio.Button value={2}>图片消息</Radio.Button>
                </Radio.Group>
              </Form.Item>
              {FormItemWrapper}
              <Divider style={{width: "175%", marginLeft: -50}} />
              <h2 className="block-title">选择群发对象</h2>
              <Form.Item label="群发粉丝">
                <Radio.Group value={kefuMessageInfo.sex} onChange={e => changeMessageValue(e.target.value, "sex")}>
                  <Radio value="all">全部粉丝</Radio>
                  <br/>
                  <Radio value="1">仅男性粉丝</Radio>
                  <Radio value="2">仅女性粉丝</Radio>
                  <Radio value="0">仅未知粉丝</Radio>
                </Radio.Group>
              </Form.Item>
              <Divider style={{width: "175%", marginLeft: -50}} />
              <h2 className="block-title">设置群发方式</h2>
              <Form.Item label="发送方式">
                <Radio.Group value={kefuMessageInfo.channel} onChange={e => changeMessageValue(e.target.value, "channel")}>
                  <Radio.Button value={0}>人工</Radio.Button>
                  <Radio.Button value={1}>延时</Radio.Button>
                  <Radio.Button value={2}>定时</Radio.Button>
                </Radio.Group>
              </Form.Item>
              {Channel}
              <Divider style={{width: "175%", marginLeft: -50}} />
              <Form.Item>
                <Button size="large" type="primary" onClick={() => submitMessage(kefuMessageInfo, history)}>确定并创建消息</Button>
              </Form.Item>
            </Form>
          </div>
        </div>
        {selectGonghaoModal && <MultiSelectGonghao allCodes={allCodes} selectGonghaoModal={selectGonghaoModal} confirmSelect={changeGonghao} codes={codes} />}
      </div>
    )
  }
  componentWillUnmount() {
    this.props.resetState()
  }
}

const mapState = state => ({
  kefuMessageInfo: state.message.kefu.kefuMessageInfo,
  content: state.message.kefu.content,
  paramsModal: state.message.kefu.paramsModal,
  allCodes: state.message.kefu.allCodes,
  codes: state.message.kefu.codes,
  selectGonghaoModal: state.message.kefu.selectGonghaoModal,
});

const mapDispatch = dispatch => ({
  initKefuMessageInfo(query) {
    dispatch(actionCreators.initKefuMessageInfo(query));

  },
  changeContentValue(e, name) {
    dispatch(actionCreators.changeContentValue(e.target.value, name));
  },
  uploadImg(kefuMessageInfo, info) {
    if (info.file.status === 'done') {
      let img = `http://t.dmmup.com//uploads/${info.file.response.filename}`,
        img_path = `./public/uploads/${info.file.response.filename}`;
      if (kefuMessageInfo.type === 0) {
        dispatch(actionCreators.changeContentValue(img, "picurl"));
      } else {
        dispatch(actionCreators.changeMessageValue(img, "img"));
        dispatch(actionCreators.changeMessageValue(img_path, "img_path"));
      }
    }
  },
  changeMessageValue(value, name) {
    dispatch(actionCreators.changeMessageValue(value, name));
  },
  addMessage() {
    dispatch(actionCreators.addMessage());
  },
  selectMessage(info, index) {
    dispatch(actionCreators.selectMessage(info, index));
  },
  resetState() {
    dispatch(actionCreators.resetState());
  },
  selectParams() {
    dispatch(actionCreators.selectParams());
  },
  hideParamsModal() {
    dispatch(actionCreators.hideParamsModal());
  },
  submitMessage(info, history) {
    dispatch(actionCreators.submitMessage(info, history));
  },
  changeGonghao(codes) {
    dispatch(actionCreators.changeGonghao(codes))
  },
  getAllCodes() {
    dispatch(actionCreators.getAllCodes())
  },
  openSelectModal() {
    dispatch(actionCreators.openSelectModal())
  },
  closeSelectModal() {
    dispatch(actionCreators.closeSelectModal())
  }
});

export default connect(mapState, mapDispatch)(EditMessage);